.header {
  background: url('/images/promo_bg.jpg') no-repeat center top fixed;
  background-clip: content-box;
  background-size: cover;
  background-position: center;
  overflow: hidden;
  width: 100%;
  min-height: 100vh;
}

h1 {
  font-size: 54px;
  color: #fff;
  font-weight: bold;
  text-align: center;
  margin-top: 0;
  padding-top: 20vh;
  margin-bottom: 20px;
}

/* Button More style */

.img {
  width: 500px;
  height: 500px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -210px 0 0 -250px;
}

.img .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: 0.5s ease;
}

.img:hover .overlay {
  display: block;
}

.img .title {
  position: absolute;
  max-width: 500px;
  left: 0;
  top: 120px;
  font-size: 24px;
  line-height: 1.5;
  text-align: center;
  color: white;
  z-index: 1;
  transition: top 0.5s ease;
}

.img:hover .title {
  top: 80px;
}

.img .button {
  position: absolute;
  width: 500px;
  top: 40vh;
  text-align: center;
  opacity: 0;
  transition: opacity 0.35s ease;
}

.img .button a {
  width: 200px;
  padding: 12px 48px;
  text-align: center;
  color: white;
  border: solid 2px white;
  border-radius: 50px;
  z-index: 1;
}

.img:hover .button {
  opacity: 1;
}

@media (max-width: 1000px) {
  header {
    padding: 20px 50px;
  }
}

@media (max-width: 700px) {
  header {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  header h2 {
    margin-bottom: 15px;
  }

