/* Hover IMG */

.videos__item {
  /*transform: translate(-50%,-50%);*/
  position: relative;
  width: 320px;
  height: 200px;
  overflow: hidden;
}

.videos__item .imgBx,
.videos__item .details {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  transition: 1.2s;
}

.videos__item .imgBx {
  top: 0;
  left: 0;
}

.videos__item:hover .imgBx {
  position: relative;
  top: 0;
  left: -100%;
}

.videos__item:hover .details {
  position: absolute;
}

.videos__item .imgBx img {
  width: 100%;
}

.videos__item .details {
  background: #262626;
  top: 0;
  left: 100%;
  padding: 20px 10px;
}

.videos__item:hover .details {
  top: 0;
  left: 0%;
}

.videos__item .details h2 {
  color: #fff;
  text-align: center;
  margin: 0;
  padding: 0 0 10px;
  border-bottom: 2px solid #fff;
  font-size: 16px;
}
.videos__item .details p {
  margin: 20px 0 0;
  color: #fff;
  font-size: 12px;
}

/* Videos wrapper */

.videos__wrapper,
.videos__wrapper2 {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
